<template>
  <BaseLayout wrapperClass="contact">
    <Breadcrumbs />
    <section class="static pt-7">
      <div class="container">
        <div class="row">
          <div class="d-none d-lg-block col-lg-3">
            <div class="sidebar">
              <span class="sidebar-heading">Links</span>
              <cmsBlockSimple identifier="side-menu-cms" />
            </div>
          </div>
          <div class="col-lg-4">
            <h1>{{ getCmsData?.title }}</h1>
            <!-- <v-runtime-template
              v-if="getCmsData?.content"
              :template="`<div class='cms-content'>${getCmsData?.content}</div>`"
            ></v-runtime-template> -->
            <div class="cms-content" v-if="getCmsData?.content" v-html="getCmsData?.content"></div>
          </div>
          <div class="col-lg-4">
            <section class="best-sellers">
              <span class="best-seller--category">{{ $t("contact_form") }}</span>
              <div class="row">
                <div class="best-seller--card-holder">
                  <div v-if="contactForm != null">
                    <DynamicForms type="contact" />
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
    </section>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useHead } from "@unhead/vue";
// import VRuntimeTemplate from "vue3-runtime-template";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import cmsBlockSimple from "@/base/core/components/core/BlockSimple";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";
import DynamicForms from "@/base/core/components/DynamicForms";

// Set up meta information using useHead
useHead({
  title: () => "Contact",
  meta: [
    { name: "title", content: () => "Contact" },
    { name: "keywords", content: () => "Contact" },
    { name: "description", content: () => "Contact" },
  ],
});

const store = useStore();

const getCmsData = computed(() => {
  return store.getters[`cmsBlock/getCmsBlockByIdentifier`]("contact_page_block");
});

const contactForm = computed(() => {
  return store.getters[`cmsBlock/getCmsBlockByIdentifier`]("contact_page_block");
});

onMounted(() => {
  const bcrumb = { current: "Contact", routes: [] };
  store.commit("breadcrumbs/set", bcrumb);
});
</script>
